import React from "react";
import { connect } from "react-redux";
import AddCity from "./AddCity";

class AddCityContainer extends React.Component {
  render() {
    return <AddCity />;
  }
}

const mapDispatchToProps = dispatch => ({
  sendTheAlert: () => {
    dispatch(ALERT_ACTION);
  }
});

const mapStateToProps = state => ({
  fancyInfo: "Fancy this:" + state.currentFunnyString
});

export default (AddCityContainer = connect(mapStateToProps, mapDispatchToProps)(
  AddCityContainer
));



